import React from 'react';
import {prefixLink} from 'gatsby-helpers';
import nbem from 'nbem';

/**
 * Ideyuta
 *
 * @return {ReactElement}
 */
export default function Ideyuta() {
  const i = nbem();
  return (
    <div className={i('Ideyuta')}>
      <div className={i('&recentPhoto')}>
        <img
          className={i('&&img')}
          src={prefixLink('/images/ideyuta.png')}
        />
        <p className={i('&&copyright')}>
          
          <a href="https://seleck.cc/850" target="_blank">RELATIONS</a>
          
        </p>
      </div>
      <h1 className={i('&hello')}>
        <b></b>
      </h1>
      <p className={i('&bio')}>
        88    1<b></b>20138ITCLO20169<a href="https://vandle.jp/" target="_blank"></a><b>20173</b> 
      </p>
      <p className={i('&bio')}>
        <a href="mailto:ide.yuta@gmail.com">ide.yuta@gmail.com</a><a href="https://github.com/ideyuta">GitHub</a><a href="https://twitter.com/_ideyuta">Twitter</a>
      </p>
      <div className={i('&trademark')}>
        <img
          className={i('&&img')}
          src={prefixLink('/images/trademark.svg')}
        />
      </div>
    </div>
  );
}



